/* Task3.css */

.fill-in-blanks-container {
  font-family: 'Comic Sans MS', cursive;
  text-align: center;
  padding: 30px;
  background-color: #f5eef8;
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}

.question-container {
  margin-top: 30px;
}

input {
  font-size: 20px;
  padding: 10px;
  margin: 0 10px;
  border: 2px solid #8b63b3;
  border-radius: 8px;
}

button {
  font-size: 18px;
  padding: 10px 20px;
  background-color: #8b63b3;
  color: #fff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 20px;
}

button:hover {
  background-color: #674386;
}

.result-message {
  font-size: 24px;
  color: #4caf50;
  margin-top: 20px;
}

.next-question {
  font-size: 18px;
  padding: 15px;
  background-color: #9270e0;
  color: #fff;
  border: none;
  border-radius: 8px;
  margin-top: 30px;
  cursor: pointer;
}

.next-question:hover {
  background-color: #f57c00;
}
